import { Directive, HostListener, ElementRef, Input, SimpleChanges } from '@angular/core';

@Directive({
  selector: '[decimalfix]'
})
export class DecimalfixDirective {
  @Input() min = -Infinity;
  @Input() max = Infinity;
  @Input() decimalSeparator = ',';
  private hasDecimalPoint = false;  
  inputElement: HTMLInputElement;

  constructor(public el: ElementRef) {
    this.inputElement = el.nativeElement;
  }

  ngOnChanges(changes: SimpleChanges): void {

    if (changes.min) {
      const maybeMin = Number(this.min);
      this.min = isNaN(maybeMin) ? -Infinity : maybeMin;
    }

    if (changes.max) {
      const maybeMax = Number(this.max);
      this.max = isNaN(maybeMax) ? Infinity : maybeMax;
    }
  }

  @HostListener('keydown', ['$event'])
    onKeyDown(e: KeyboardEvent): any {
      if (
        ((e.key === ',') && !this.hasDecimalPoint) // Allow: only one decimal point
      ) {
        // let it happen, don't do anything
        return;
      }
      else if ((e.key === this.decimalSeparator) && this.hasDecimalPoint) {
        e.preventDefault();
      }
  
      if (isNaN(Number(e.key))) {
        e.preventDefault();
      }

      const oldValue = this.inputElement.value;
      let newKey = e.key==','?'.':e.key;

      const newValue = (oldValue + newKey);
      console.log(oldValue, newValue);
  
      if (parseFloat(newValue) > this.max || parseFloat(newValue) < this.min) {
        e.preventDefault();
      }
    }
  @HostListener('keyup', ['$event'])
  onKeyUp(e: KeyboardEvent): void {
    this.updateDecimalPoint();
  }

  updateDecimalPoint(): void {
    this.hasDecimalPoint =
      (this.inputElement.value.includes(',') || this.inputElement.value.includes('.'));
  }
}
