import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Invoice } from 'src/assets/class-templates/invoice';
import { DataInvoice } from 'src/assets/class-templates/datainvoice';
import { UrlService } from './url.service';
import { Router } from '@angular/router';
import { reject } from "q";

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  constructor(private http: HttpClient, private urlService: UrlService, private router: Router) { }

  private headers = new HttpHeaders({ 'Authorization': 'Bearer '+localStorage.getItem("token") });

  checkToken(res): void {
    if (res.status === 401) {
      localStorage.removeItem("token");
      this.router.navigate(["/"]);
      window.alert("Kullanıcı izni geçersiz!");
    }
  }

  //Cancel service
  async cancelInvoice(invoice: Invoice) {

    return this.http.put(this.urlService.getUrl("invoices", invoice.id), {"status":-1}, {headers:this.headers}).toPromise()
      .then(res => {
        console.log("success",res);
      })
      .catch(res => {
        console.log("Eyvah", res);
        this.checkToken(res);
      });
  }

  //Invoice service
  async postInvoice(invoice) {
    const headers = new HttpHeaders({ 'Authorization': 'Bearer '+localStorage.getItem("token") });
    return this.http.post(this.urlService.getUrl("invoices"), invoice, {headers}).toPromise()
      .then(res => {
        console.log("success",res);
        return res;
      })
      .catch(res => {
        console.log("Eyvah", res);
        this.checkToken(res);
        return reject(res.error.message);      
      });
  }

  async updateInvoice() {
    return this.http.post(this.urlService.getUrl("invoices/files"), {}, {headers: this.headers}).toPromise()
      .then(res => {
        console.log("success",res);
        return res;
      })
      .catch(res => {
        console.log("Eyvah", res);
        this.checkToken(res);
        return reject(res.error.message);
      })
  }

  //List service
  getList() {
    return this.http.get<DataInvoice>(this.urlService.getUrl("invoices"), {headers: this.headers})
    .toPromise()
      .then(res => {
        console.log("success",res);
        return res;
      })
      .catch(res => {
        console.log("Eyvah", res);
        this.checkToken(res);
        return res;
      });
  }

  //Login service 
  checkUser(username: string, password: string) {
    const user = {loginName: username, password: password};

    return this.http.post(this.urlService.getUrl("session"), user).toPromise()
        .then(res=>{
          localStorage.setItem("token", res["data"].token);})
        .catch(res=>{
          console.log("Hebele", res);
          localStorage.removeItem("token");})
  }
}
