import { Component, OnInit } from '@angular/core';
import { Invoice } from 'src/assets/class-templates/invoice';
import { FileService } from '../file.service';
import { Subject } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { AlertComponent } from '../alert/alert.component';
import { UrlService } from '../url.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.css'],
  providers: [AlertComponent]
})
export class ListComponent implements OnInit {

  listEmpty: boolean = true;
  invoices: Invoice[];
  filteredItems = [];
  filteredItemsId = [];
  filteredItemsName = [];
  filterOption: string = 'all';
  searchValueId: string = '';
  searchValueName: string = '';

  constructor(
    private fileService: FileService, private urlService: UrlService,
    private alertComponent: AlertComponent) { }

  ngOnInit() {
    this.urlService.httpGet("invoices")
    .then(res=>{
      this.invoices = res.data;
    }).finally(()=>
      this.filteredItems = this.invoices);
  }

  cancelInvoice(invoice: Invoice): void {
    let accept = confirm("Silmek istiyor musunuz?");

    if (accept) {
      invoice.status = -1;

      this.urlService.httpPut("invoices", {"status": -1}, invoice.id);
      
      this.filteredItems = this.filteredItems.filter(p=>p.status != -1);
    }
  }

  exportAsXLSX(data:Invoice):void {
    let arrayData = this.fileService.ekolMaker(data);
    this.fileService.exportCompanyFile(arrayData, 'company');
    this.fileService.exportGoodsFile(data, 'goods');
  }

  exportAllAsXLSX():void {
    this.invoices.forEach(invoice => {
      if (invoice.status == 1) {
        let arrayData = this.fileService.ekolMaker(invoice);
        this.fileService.exportCompanyFile(arrayData, 'company');
        this.fileService.exportGoodsFile(invoice, 'goods');
      }
    })
  }

  async updateResults() {
    this.filteredItems = this.invoices;
    this.filteredItemsId = this.filterByOption(this.searchById(this.invoices));
    this.filteredItemsName = this.filterByOption(this.searchByName(this.invoices));
    this.filteredItems = this.filteredItems.filter(value => this.filteredItemsName.includes(value) 
      && this.filteredItemsId.includes(value));
  }

  searchById(items:Invoice[]) {
    return items.filter(item => {
      if (this.searchValueId.trim() === '') {
        return true;
      } else {
        return item.id.toLowerCase().includes(this.searchValueId.trim().toLocaleLowerCase());
      }
    })
  }

  searchByName(items:Invoice[]) {
    return items.filter(item => {
      if (this.searchValueName.trim() === '') {
        return true;
      } else {
        return item.customerInfo.name.toLowerCase().includes(this.searchValueName.trim().toLocaleLowerCase()) 
          || item.customerInfo.lastname.toLowerCase().includes(this.searchValueName.trim().toLocaleLowerCase());
      }
    })
  }

  filterByOption(items: Invoice[]) {
    return items.filter(item => {
      if (this.filterOption === 'all' || this.filterOption === item.status.toString()) {
        return true;
      } 
    })
  }

  updateInvoice() {
    this.urlService.httpPost("invoices/files", {})
      .then((res: {data: Invoice[]}) => {
        if (res.data.length !== 0) {
          res.data.forEach(invoice =>{
            let index = this.invoices.indexOf(invoice);
            if (index !== -1) {
              this.invoices[index] = invoice;
            }
          })
          this.updateResults();
          this.alertComponent.addToast("Senkronizasyon Başarılı.", "success");
        }
        else {
          this.alertComponent.addToast("Senkronizasyon için dosya bulunamadı.", "error");
        }
      })
  }
}
