import { Component, OnInit } from '@angular/core';
import { Products } from '../../assets/class-templates/products';
import { CountryService, Country } from '../../assets/services/country.service';
import { NgForm } from '@angular/forms';
import { Invoice } from 'src/assets/class-templates/invoice';
import { Shipment } from 'src/assets/class-templates/shipment';
import { MaterialService, Material } from 'src/assets/services/material.service';
import { AlertComponent } from "../alert/alert.component";
import { UrlService } from '../url.service';

@Component({
  selector: 'app-create',
  templateUrl: './create.component.html',
  styleUrls: ['./create.component.css'],
  providers: [AlertComponent]
})
export class CreateComponent implements OnInit {

  payForm = new Invoice;
  shipmentInfo = new Shipment;
  isSame: boolean = true;
  materials: Material[];
  countries: Country[];
  selectedCountry: Country;
  selectedCountryS: Country;
  selectTC: string = "true";
  errorMessage: string;
  total: number = 0;
  totalDiscount: number = 0;
  totalDiscountGeneral: number = 0;
  netTotal: number = 0;
  serviceNotDone: boolean = false;

  constructor(
    private countryservice: CountryService, private urlService: UrlService,
    private materialService: MaterialService, private alertComponent: AlertComponent
  ) { }

  ngOnInit() {
    this.materials = this.materialService.getMaterials();
    this.countries = this.countryservice.getCountries();
  }

  appendProduct(): void {
    let products = new Products();
    this.payForm.products.push(products);
  }

  formSubmitted: boolean = false;

  formOut(form: NgForm) {
    this.formSubmitted = true;
    if (form.valid) {
      this.clean(this.payForm);
      this.serviceNotDone = true;
      this.payForm.customerInfo.country = this.selectedCountry.name;
      this.payForm.customerInfo.countryCode = this.selectedCountry.code;   
      if (!this.isSame) {
        this.shipmentInfo.country = this.selectedCountryS.name;
        this.shipmentInfo.countryCode = this.selectedCountryS.code; 
      }
      else {
        let shipmentIn = {...this.payForm.customerInfo};
        delete shipmentIn.name;
        delete shipmentIn.lastname;
        delete shipmentIn.taxDepartmentNumber;
        delete shipmentIn.identityNumber;
        this.shipmentInfo = {...shipmentIn}
      }
      this.payForm.shipmentInfo = this.shipmentInfo;
      this.urlService.httpPost("invoices", this.payForm)
        .then((res:{data:string})=>{
          window.alert("Fatura başarıyla oluşturuldu. Fatura numaran: " + res.data);
          this.payForm = new Invoice;
          this.shipmentInfo = new Shipment;
          this.selectedCountryS = {"code": null, "name": null};
          this.selectedCountry = {"code": null, "name": null};
          this.formSubmitted = false;
          form.reset();
          this.serviceNotDone = false;
        })
        .catch(msg=>{        
          this.alertComponent.addToast("Servis isteğinizi reddetti."+msg, 'error')
          this.formSubmitted = false;
          this.serviceNotDone = false;
        });
    }
    else {
      this.alertComponent.addToast("Kırmızı ile işaretlenmiş alanı doldurmalısınız.", 'error')
    }
    
  }

  deleteProduct(product: Products): void {
    this.payForm.products = this.payForm.products.filter(p=>p !== product);
  }

  deleteDiscount(product: Products, discount): void { 
    const index: number = product.discounts.indexOf(discount);
    if (index !== -1) {
      product.discounts.splice(index, 1);
    }        
    this.calculateSummary();
  }

  addDiscount(product: Products): void {
    if (product.discounts.length < 3) {
      product.discounts.push(null);
    }
  }

  addGeneralDiscount() {    
    if (this.payForm.discounts.length < 3) {
      this.payForm.discounts.push(null);
    }
  }

  trackByFn(index: any, item: any) {
    return index;
 }

  calculateSummary() {
    this._total();
    this._discount();
    this._net();
    this._discountGeneral();
    this.netTotal -= this.totalDiscountGeneral;
  }

 _total() {
    this.total = 0;
    this.payForm.products.forEach(product =>{
      this.total += (product.price ? product.price : 0) * product.quantity;
    })
 }

 _discount() {   
    this.totalDiscount = 0;
    this.payForm.products.forEach(product =>{
      let newPrice = product.price * product.quantity;
      product.discounts.forEach(discount => {
        this.totalDiscount += (newPrice * discount) /100;
        newPrice -= (newPrice * discount) /100;
      })
    })
 }

 _discountGeneral() {
    this.totalDiscountGeneral = 0;
    let newPrice = this.netTotal;
    this.payForm.discounts.forEach(discount => {
      this.totalDiscountGeneral += (newPrice*discount) /100;
      newPrice -= (newPrice*discount) /100;
   })
 }

 _net() {
    this.netTotal = this.total-this.totalDiscount;
 }

 clean(obj: Object):void {
   for (var propName in obj) {
    if (typeof obj[propName] == "object" && !(Array.isArray(obj[propName]) && obj[propName].length == 0)) {
      this.clean(obj[propName]);
    }
    else if (obj[propName] === undefined || obj[propName] == "" || obj[propName] === null || obj[propName].length == 0 ) {
      delete obj[propName];
    }
   }
 }

 autoFill(obj: Object): void {
  for (var propName in obj) {
    if (typeof obj[propName] == "object") {
      this.autoFill(obj[propName]);
    }
    else if ( typeof obj[propName] == "number" ) {
      obj[propName] = 1;
    }

    else if (typeof obj[propName] === "string") {
      obj[propName] = "Hebele";
    }
   }
 }
}
